body {
  background-color: #214c4f;
  font-family: 'Arial', sans-serif;
  color: #ffffff;
}

.container {
  max-width: 600px;
  margin: 0 auto;
}

h1 {
  text-align: center;
  font-family: 'Georgia', serif;
  font-weight: bold;
  color: white;
  margin-bottom: 40px;
  font-size: 2.5rem;
}

/* Interactive Background */
.interactive-background {
  background: linear-gradient(135deg, #2c3e50, #4ca1af);
  animation: gradient-animation 15s ease infinite;
}

@keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Length Control Flexbox */
.length-controls {
  display: flex;
  justify-content: space-around;
  margin: 50px auto;
}

.length-control {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Label Container */
.label-container {
  font-size: 1.25rem;
  margin-bottom: 10px;
  font-weight: bold;
  color: #ffffff;
}

/* Value Container */
.value-container {
  font-size: 2rem;
  margin: 0 20px;
  font-weight: bold;
  color: #ffffff;
}

/* Button Styling */
.btn-decrement,
.btn-increment,
.btn-control {
  background-color: transparent;
  color: #ffffff;
  border: 2px solid #ffffff;
  font-size: 1.5rem;
  padding: 5px 15px;
  margin: 10px;
  border-radius: 5px;
  transition: all 0.2s ease;
}

.btn-decrement:hover,
.btn-increment:hover,
.btn-control:hover {
  background-color: #ffffff;
  color: #2c3e50;
  transform: scale(1.1);
}

/* Timer Display */
.timer-display {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: rgba(255, 255, 255, 0.1);
  border: 2px solid #ffffff;
  border-radius: 20px;
  color: #ffffff;
  padding: 30px;
}

.display-1 {
  font-size: 4rem;
  font-weight: bold;
}

.display-4 {
  font-size: 2rem;
  margin-bottom: 10px;
}
.control-buttons {
  display: flex;
  justify-content: center; /* Center the buttons */
  margin-top: 20px; /* Add margin above the buttons */
}
.d-flex {
  display: flex;
}

/* Footer Styling */
.footer {
  margin-top: 20px;
  color: #b1b1b1;
  font-size: 0.75rem;
  text-align: center;
}
